/* Styling by ID attribute */
#quote-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100vw;
  height: 100vh;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

}

/* Styling by class */
.top-section {
  background: #f4f4f4;
  width: 50%;
  height: 25%;
  text-align: center;
}

.top-section p {
  padding-top: 20px;
}

.bottom-section {
  background: black;
  width: 50%;
  height: 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.bottom-section a {
  margin-right: 25px;
  font-size: 16px;
  background: #ffffff;
  color: #000000;
  padding: 5px;
}


